import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import 'bootstrap/dist/css/bootstrap.css';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs.js';
import TabContent from 'react-bootstrap/TabContent';
import HomePage from './homepage';
import Sensor from './sensor';
import FormPage from './formPage';

class Header extends React.Component {
    render() {
        var textStyle = {
            fontSize: 28,
            fontFamily: "sans-serif",
            color: "#333",
            fontWeight: "bold",
            weight: "100%",
            height: "10%",
            padding: "20px"
        };
        var hrStyle = {
            marginTop: "0.4rem",
            borderTop: "1.2px solid #8c8b8b"
        }


        return (
            <div style={textStyle}>
              Data Health Dashboard
            <hr style={hrStyle}/>
            </div>
    );
    }
}
class TabsSection extends React.Component {
    //Note that indivi tab stryle has a harcoded height value so that might make things look weird
    // Fix with changing bootstrap tab style to have height:100%
    render(){
        var tabStyle = {
            border: "15px",
            width: "95%",
            margin: "auto"
        }
        var indivisualTabStyle = {
            margin: "auto",
            width: "95%",
            height: "625px",
            padding: "15px"
        }
        return (
            <div style={tabStyle}>
            <Tabs defaultActiveKey="home" id="uncontrolled-tab-example">
                <Tab eventKey="home" title="Home" style={indivisualTabStyle}>
                    <HomePage/>
                </Tab>
                <Tab eventKey="Sensor" title="Sensor" style={indivisualTabStyle}>
                    <Sensor/>
                </Tab>
                <Tab eventKey="Form" title="Form" style={indivisualTabStyle}>
                    <FormPage/>
                </Tab>
            </Tabs>
            </div>
        );
    }

}

var overallStyle = {
    height: "750px"
}
ReactDOM.render(
<div style={overallStyle}>
<Header/>
<TabsSection/>
    </div>,
document.querySelector("#container")
);